.lc-designer-footer {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.lc-designer-footer .footer-left {
  display: flex;
}
.lc-designer-footer .footer-left .footer-item {
  height: 100%;
  font-size: 12px;
  line-height: 37px;
  color: #c4c4c4;
  padding: 0 10px;
  cursor: pointer;
  border-top-style: solid;
  border-top-width: 3px;
  border-top-color: rgba(255, 28, 28, 0);
  transition: color 0.5s, background-color 0.5s, border-top-color 0.5s;
}
.lc-designer-footer .footer-left .footer-item .item-content {
  position: relative;
  bottom: 3px;
}
.lc-designer-footer .footer-left .footer-item:hover {
  color: #62f0ff;
  border-top-color: #62f0ff;
  background-color: rgba(44, 246, 255, 0.27);
}
.lc-designer-footer .footer-right {
  display: flex;
}
.lc-designer-footer .footer-right .right-info-item {
  color: #c4c4c4;
  margin-right: 20px;
}
