.layer-item {
  color: #bebebe;
  padding: 3px 10px;
  font-size: 12px;
  height: 40px;
  background: #363636;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  cursor: pointer;
}
.layer-item .layer-item-operators {
  display: flex;
}
.layer-item .layer-item-operators .layer-item-operator {
  margin-left: 5px;
  cursor: pointer;
}
.layer-item .layer-item-operators .layer-item-operator img {
  width: 14px;
}
.layer-item:hover {
  background: #435266;
}
.layer-item-selected {
  background: #435266;
}
