.float-panel {
  z-index: 1;
  width: 180px;
  height: calc(100% - (50px + 40px));
  position: absolute;
  background-color: #1c1f22;
}
.float-panel .panel-title {
  height: 30px;
  display: flex;
  color: #daf4fb;
  border-radius: 0;
  padding: 3px 5px;
  align-items: center;
  justify-content: space-between;
  background-color: #26292e;
}
.float-panel .panel-title .title-drag-target:hover {
  cursor: move;
}
.float-panel .panel-title .title-right:hover {
  cursor: pointer;
}
