.designer-header {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.designer-header .header-left .header-title {
  color: #00d6ff;
  font-size: 20px;
  font-weight: 700;
  padding-left: 15px;
}
.designer-header .header-right {
  display: flex;
}
.designer-header .header-right .right-item {
  height: 100%;
  font-size: 12px;
  color: #b7b1b1;
  padding: 0 10px;
  line-height: 50px;
  cursor: pointer;
  transition: color 0.5s, background-color 0.5s, border-bottom-color 0.5s;
  border-bottom-style: solid;
  border-bottom-width: 6px;
  border-bottom-color: rgba(255, 28, 28, 0);
}
.designer-header .header-right .right-item .item-span {
  position: relative;
  top: 3px;
}
.designer-header .header-right .right-item:hover {
  background-color: rgba(44, 246, 255, 0.27);
  color: #62f0ff;
  border-bottom-color: #62f0ff;
}
