.lc-designer-structure {
  height: 100%;
  overflow: hidden;
  position: relative;
}
.lc-designer-structure .structure-header {
  height: 50px;
  background-color: #111216;
}
.lc-designer-structure .structure-body {
  display: flex;
  justify-content: space-between;
  height: calc(100% - (50px + 40px));
  background-color: #001528;
}
.lc-designer-structure .structure-body .structure-body-left {
  width: 60px;
  height: calc(100% + 40px);
  background-color: #111216;
  overflow-y: scroll;
  position: relative;
}
.lc-designer-structure .structure-body .structure-body-content {
  width: calc(100% - (60px + 35px));
  background-color: #0c162d;
}
.lc-designer-structure .structure-body .structure-body-right {
  width: 35px;
  background-color: #1b1e23;
  z-index: 1;
}
.lc-designer-structure .structure-foot {
  position: relative;
  left: 60px;
  width: calc(100% - 60px);
  height: 40px;
  background-color: #111216;
}
