.lc-config-panel {
  z-index: 1;
  animation: lcFadeInUp 0.7s forwards cubic-bezier(0.175, 0.885, 0.32, 1.275);
  width: 330px;
  top: 50px;
  right: 35px;
  position: absolute;
  height: calc(100% - 90px);
  background-color: #1b1e23;
}
.lc-config-panel .lc-panel-top {
  width: 100%;
  height: 30px;
  display: flex;
  color: #eaeef8;
  padding: 5px 10px;
  align-items: center;
  background-color: #26292e;
  justify-content: space-between;
}
.lc-config-panel .lc-panel-content {
  height: calc(100% - 30px);
  overflow-y: auto;
  padding: 10px;
}
@keyframes lcFadeInUp {
  0% {
    z-index: -1;
    opacity: 0;
    transform: translateX(35px);
  }
  50% {
    z-index: -1;
    opacity: 1;
    transform: translateX(35px);
  }
  55% {
    z-index: -1;
    opacity: 1;
    transform: translateX(35px);
  }
  100% {
    z-index: -1;
    opacity: 1;
    transform: translateX(0);
  }
}
