.lc-right-menu {
  background-color: #1b1e23;
  padding: 5px;
  width: 130px;
  min-height: 150px;
  transition: opacity 250ms ease !important;
}
.lc-right-menu .menu-item {
  display: flex;
  color: rgba(206, 206, 206, 0.86);
  font-size: 13px;
  padding: 5px 5px 5px 10px;
}
.lc-right-menu .menu-item label {
  margin-right: 10px;
}
.lc-right-menu .menu-item label,
.lc-right-menu .menu-item span {
  pointer-events: none;
}
.lc-right-menu .menu-item:hover {
  background-color: #6b6b6b;
}
